.tabs {
  &__navs {}

  &__nav {
    user-select: none;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    &--active {
      cursor: default;
    }
  }

  &__tabs {}

  &__tab {
    display: none;

    &--active {
      display: block;
    }

    &--animation {
      &-cancel {
        animation: none !important;
      }
      &-show {
        animation: fadeIn 150ms both;
      }
      &-hide {
        animation: fadeOut 150ms both;
      }
    }
    &--transition {
      &-cancel {
        transition-duration: 0s !important;
      }
      &-show {
        opacity: 1;
      }
      &-hide {
        opacity: 0;
      }
    }
  }

  &--without-animation {
    .tabs {
      &__tab {
        opacity: 1;
      }
    }
  }
  &--animation {
    .tabs {
      &__tab {
        transition-duration: 0s;
        opacity: 1;
      }
    }
  }
  &--transition {
    .tabs {
      &__tab {
        transition-duration: 150ms;
        opacity: 0;
      }
    }
  }
}